<template>
  <div class="star" :class="starType">
   	<span v-for="itemClass in itemClasses" :class="itemClass" class="starClass"></span>
  </div>
</template>

<script>
const LENGTH=5
const CLS_ON='on'
const CLS_HALF='half'
const CLS_OFF='off'
export default {
  props:{
  	size:{
  		type:Number
  	},
  	score:{
  		type:Number
  	}
  },
computed:{
	starType(){
		console.log('star-'+this.size)
		return 'star-'+this.size
	},
	itemClasses(){
		let result=[]
		let score=Math.floor(this.score*2)/2
		let hasDecimal=score%1 !== 0
		let integer=Math.floor(score)
		for(let i=0;i<integer;i++){
			result.push(CLS_ON)
		}
		if(hasDecimal){
			result.push(CLS_HALF)
		}
		while(result.length<LENGTH){
			result.push(CLS_OFF)
		}
		console.log(result)
		return result
	}
},




}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.star{
		font-size: 0px;
	}
	.starClass{
		display: inline-block;
		background-repeat: no-repeat;
	}
	.star-24{
		display: inline-block;
	}
	.star-24 .starClass{
		margin-right: 3px;
		width: 10px;
		height: 10px;
		background-size: 10px 10px;
	}
	.star-24 .starClass:last-child{
		margin-right: 0px;
	}	
	.star-24 .on{
		background-image: url('star24_on@2x.png');
	}
	.star-24 .half{
		background-image: url('star24_half@2x.png');
	}
	.star-24 .off{
		background-image: url('star24_off@2x.png');
		
	}

	.star-36{
		display: inline-block;
	}
	.star-36 .starClass{
		margin-right: 6px;
		width: 15px;
		height: 15px;
		background-size: 15px 15px;
	}
	.star-36 .starClass:last-child{
		margin-right: 0px;
	}	
	.star-36 .on{
		background-image: url('star36_on@2x.png');
	}
	.star-36 .half{
		background-image: url('star36_half@2x.png');
	}
	.star-36 .off{
		background-image: url('star36_off@2x.png');
		
	}

	.star-48{
		display: inline-block;
	}
	.star-48 .starClass{
		margin-right: 22px;
		width: 20px;
		height: 20px;
		background-size: 20px 20px;
	}
	.star-48 .starClass:last-child{
		margin-right: 0px;
	}	
	.star-48 .on{
		background-image: url('star48_on@2x.png');
	}
	.star-48 .half{
		background-image: url('star48_half@2x.png');
	}
	.star-48 .off{
		background-image: url('star48_off@2x.png');
		
	}
</style>



