<template>
	<div>
  <div class="shopcart">
   	<div class="content" @click="toggleList">
   		<div class="content-left">
   			<div class="logn-wrapper">
   				<div class="logn" :class="{highLight:totalCount>0}">
   					<i class="icon-shopping_cart" :class="{highLight:totalCount>0}"></i>
   				</div>
   				<div class="num" v-show="totalCount>0">{{totalCount}}</div>			
   			</div>
   			<div class="price" :class="{highLight:totalPrice}">￥{{totalPrice}}</div>
   			<div class="desc">另所需配送费{{deliveryPrice}}元</div>
   		</div>
   		<div class="content-right" @click.stop.prevent="pay">
   			<div class="pay" :class="payClass">{{payDesc}}</div>
   		</div>
   	</div>
		<div class="ball-container">
			<div class="ball" v-for="ball in dropBalls">
				<transition name="ballMove"  v-on:before-enter="beforeEnter" v-on:enter="enter" v-on:after-enter="afterEnter">
				<div v-show="ball.show">
					<div class="inner">
						1
					</div>
				</div>
			</transition>
			</div>
		</div>
		<transition name="fade1" >
   		<div class="shopcart-list" v-show="fold">
   		<div class="list-header">
   			<h1 class="title">购物车</h1>
   			<span class="empty" @click="empty">清空</span>
   		</div>
   		<div class="list-content" ref="listContent">
   			<ul>
   				<li class="food" v-for="food in selectFoods">
   					<span class="name">{{food.name}}</span>
   					<div class="price">
   						<span >￥{{food.count*food.price}}</span>
   					</div>
   					
   					<div class="cartcontrol-wrapper">
   						<cartcontrol :food="food" @hideMark="hidedMark"></cartcontrol>
   					</div>
   				</li>
   			</ul>
   		</div>
   	</div>
   </transition>
  </div>
  <transition name="mark">
  <div class="list-mark" v-show="markBool" @click="hiddenMark">  </div>
</transition>
  </div>
</template>

<script>
import cartcontrol from '../cartcontrol/cartcontrol'
import BScroll from 'better-scroll'
 let n=0
export default {

  props:{
  	deliveryPrice:{
  		type:Number,
  		default:0
  	},
  	minPrice:{
  		type:Number,
  		default:0
  	},
  	selectFoods:{
  		type:Array,
  		default(){
  			return [
  				{
  				price:10,
  				count:1

  			},
  			{
  				price:100,
  				count:7
  			}
  			
  			]
  		}
  	}
  },
  data(){
  	return{
  		fold:false,
  		bool:false,
  		markBool:false,
  		dropBalls:[
  		{show:false},
  		{show:false},
  		{show:false},
  		{show:false},
  		{show:false}
  		],
  		getPosition:[],
  		dorpeds:[]
  	}
  },
  components:{
  	cartcontrol
  },
  methods:{
  	hidedMark(){
  		if(this.totalCount==0){
  		this.markBool=false
  		this.fold=false
  		this.bool=false
  		}
  		
  	},
  drop(value){
  	this.getPosition=value
  	for(let i=0;i<this.dropBalls.length;i++){
  		if(!this.dropBalls[i].show){
  			this.dropBalls[i].show=true
  			this.dorpeds.push(this.dropBalls[i])
  			return
  			
  		}
  		
  	}
 
  },
  beforeEnter(el){
  	
  	el.style.transform=`translate3d(${this.getPosition[1]-16}px,-${window.innerHeight-this.getPosition[0]-32}px,0)`
  	el.style.opacity=1
  },
  enter(el,done){
  		 Velocity(el, {opacity:0,transform:"translate3d(32px,22px,0)"})
  		 el.style.transition="all 0.5s cubic-bezier(.8,-0.87,0,1.02)"
  		 setTimeout(()=>{
  		 	done()
  		 },500)		 
  },
  afterEnter(el){
  	let ball=this.dorpeds.shift()
  	if(ball){
  		ball.show=false
  		el.style.display="none"
  	}

  },
  	pay(){
  		if(this.minPrice>this.totalPrice){
  			return
  		}
  		window.alert(`支付￥${this.totalPrice}`)
  	},
  	hiddenMark(){
  		if(!this.totalCount){
  			return false
  		}
  		if(this.markBool==true){
  			this.bool=!this.bool
  		this.markBool=!this.markBool
  		}
  		
  	},
  	toggleList(){
  		if(!this.totalCount){
  			return
  		}
  		this.markBool=!this.markBool
  		this.fold=!this.fold
  		
  	},
  	empty(){
  		this.selectFoods.forEach((food)=>{
  			food.count=0

  		})
  			this.fold=!this.fold
  			this.markBool=false
  	}
  },
  computed:{
  	totalPrice(){
  		let total=0
  		this.selectFoods.forEach((food)=>{
	  	total+=food.price*food.count
  		})
  		return total
  	},
  	totalCount(){
  		let count=0
  		this.selectFoods.forEach((food)=>{
  			count+=food.count
  		})
  		return count
  	},
  	payDesc(){
  		if(this.totalPrice===0){
  			return `￥${this.minPrice}起送`
  		}else if(this.minPrice-this.totalPrice>0){
  			let diss=this.minPrice-this.totalPrice
  			return `还差￥${diss}元起送`
  		}else{
  			return '去节算'
  		}
  	},
  	payClass(){
  		if(this.minPrice-this.totalPrice>0){
  			return 'no-enough'
  		}else if(this.totalPrice-this.minPrice>=0){
  			return 'enough'
  		}
  	},
  	listSHow(){
  		if(!this.totalCount){
  			return false
  		}
  		let show=!this.fold
  		if(show){
  			this.$nextTick(()=>{
  				if(!this.scroll){
  					this.scroll=new BScroll(this.$refs.listContent,{
  					click:true
  				})
  				}else{
  					this.scroll.refresh()
  				}
  				
  			})
  		}
  		return show
  	}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.list-mark{
		position: fixed;
		top:0px;
		left: 0px;
		z-index: 40;
		backdrop-filter:blur(10px);
		width: 100%;
		height: 100%;
		background: rgba(7,17,27,0.5);
	}
	.shopcart{
		position: fixed;
		bottom: 0px;
		left: 0px;
		z-index: 50;
		height: 48px;
		width: 100%;
		
	}
	.shopcart .content{
		background: #141d27;
		font-size: 0px;
		display: flex;
		color: rgba(255,255,255,.4);
	}
	.shopcart .content .content-left{
		flex: 1;
		
	}
	.shopcart .content .content-left .logn-wrapper{
		display: inline-block;
		position: relative;
		box-sizing: border-box;
		top: -10px;
		padding: 6px;
		margin:0px 12px;
		border-radius: 50%;
		vertical-align: top;
		text-align: center;
		height: 56px;
		width: 56px;
		background: #141d27;

	}
	.shopcart .content .content-left .logn-wrapper .logn{
		border-radius: 50%;
		width: 100%;
		height: 100%;
		background:#2b343c;
		text-align: center;


		
	}
	.shopcart .content .content-left .logn-wrapper .logn.highLight{
		background:rgb(0,160,220);


	}
	.shopcart .content .content-left .logn-wrapper .num{
		position: absolute;
		top: 0px;
		right: 0px;
		height: 16px;
		z-index: 10;
		text-align: center;
		width: 24px;
		border-radius: 12px;
		background: rgb(240,20,20);
		box-shadow: 0px 4px 8px 0px rgba(0,0,0,.4);
		line-height: 16px;
		font-size: 9px;
		font-weight: 700;
		color: rgb(255,255,255);

	}
	.shopcart .content .content-left .logn-wrapper .logn .icon-shopping_cart{
		font-size: 24px;
		line-height: 44px;
		color:#80858a;
		

	}
	.shopcart .content .content-left .logn-wrapper .logn .icon-shopping_cart.highLight{
		color: #fff;
	} 
	.shopcart .content .content-left .price{
		display: inline-block;
		vertical-align: top;
		box-sizing: border-box;
		margin-top: 12px;
		line-height: 24px;
		padding-right: 12px;
		border-right: 1px solid rgba(255,255,255,0.1);
		font-size: 16px;
		font-weight: 700;
	}
	.shopcart .content .content-left .price.highLight{
		color: #fff;
	}
	.shopcart .content .content-left .desc{
		display: inline-block;
		vertical-align: top;
		padding-left: 12px;
		font-size: 14px;
		line-height: 48px;
		height: 48px;
		
	}
	.shopcart .content .content-right{
		flex: 0 0 105px;
		width: 105px;
	}
	.shopcart .content .content-right .pay{
		text-align: center;
		line-height: 48px;
		height: 48px;
		line-height: 48px;
		font-size: 12px;
		font-weight: 700;
		background: #2b333b;
	
	}
	.shopcart .content .content-right .pay.no-enough{
		background: #2b333b;
		color: rgba(255,255,255,.4); 
	}
	.shopcart .content .content-right .pay.enough{
		background: #00b43c;
		color: #fff;
	}

/*掉落的球*/
	.shopcart .ball-container .ball{
		position: fixed;
		left: 32px;
		bottom: 22px;
		z-index: 200;
	}
	.shopcart .ball-container .ball .inner{
		border-radius: 50%;
		height: 16px;
		width: 16px;
		text-align: center;
		color: #fff;
		font-size: 9px;
		line-height: 16px;
		background: rgb(0,160,220);
	}

	.shopcart .shopcart-list{
		position: absolute;
		top: 0px;
		left: 0px;
		width: 100%;
		z-index: -1;
		transform: translate3d(0,-100%,0);
		
	}
	
	.shopcart .shopcart-list .list-header{
		background:#f3f5f7;
		padding: 0px 18px;
		height: 40px;
		line-height: 40px;
		background: #f3f5f7;
		border-bottom: 1px solid rgba(7,17,27,0.1);

	}
	.shopcart .shopcart-list .list-header .title{
		float: left;
		font-size: 14px;
		color: rgb(7,17,27);
	}
	.shopcart .shopcart-list .list-header .empty{
		float: right;
		font-size: 14px;
		color:rgb(0,160,220);
	}
	.shopcart .shopcart-list .list-content {
		padding:0 18px;
		max-height: 217px;
		overflow: hidden;
		background: #fff;
	}
	.shopcart .shopcart-list .list-content .food{
		position: relative;
		padding: 12px 0;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	.shopcart .shopcart-list .list-content .name{
		line-height: 24px;
		font-size: 14px;
		color:rgb(7,17,27);

	}
	.shopcart .shopcart-list .list-content .price{
		position: absolute;
		right: 90px;
		bottom: 12px;
		line-height: 24px;
		font-size: 14px;
		font-weight: 700;
		color: rgb(240,20,20);

	}
	.shopcart .shopcart-list .list-content .cartcontrol-wrapper{
		position: absolute;
		right: 0px;
		bottom: 6px;
	}
	
	/*向上的动画*/
	.mark-enter-active,.mark-leave-active{
		transition: all 1s;
		background: rgba(7,17,27,0.5);
	}
	.mark-enter,.mark-leave-to{
		opacity: 0;
		background: rgba(7,17,27,0);
	}

	/*.ballMove-enter-active{
		
		transition: all 0.5s;
	}*/
.fade1-enter-active, .fade1-leave-active {
		
		opacity: 1;
 		 transition: all 1s linear;
 	
}
.fade1-enter, .fade1-leave-to  {
 opacity: 0;

}
</style>



