<template>
 <div id="app">
    
   <v-header v-bind:seller='seller'></v-header> 
  <div class="tab">
    <div class="tab-item   ">
        <router-link to="/goods" >商品</router-link>
    </div>
    <div class="tab-item  ">
        <router-link to="/rating" >评论</router-link>
    </div>
    <div class="tab-item">
      <router-link to="/seller" >商家</router-link>
    </div>
  </div>
  <div class="content">

  </div>
  <router-view :seller="seller"></router-view>
 </div>
</template>

<script>
import header from './components/header/header'
import "./common/css/style.css"

export default {
  components:{
    'v-header':header,
  },
  data(){
   return{
      seller:{}
   }
  },
  created(){
    this.$http.get('https://wd7517823586mbplep.wilddogio.com/seller.json')
    .then((res)=>{
      if(res.ok){
     this.seller=res.body
     console.log(this.seller)
      }
    
    
    })
  }
}
</script>

<style scoped>
  #app .tab{
    display:flex;
    width: 100%;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid rgba(7,17,27,0.1);
    
  }
  #app .tab .tab-item{
    flex: 1;
    text-align: center;
    
  }
  #app .tab .tab-item a{
    display: block;
    color: rgb(77,85,93);
    font-size: 14px;
  }
  
  #app .tab .tab-item .router-link-active{
      color:rgb(240,20,20);
   }

</style>



