<template>
  <div class="goods">
   <div class="menu-wrapper"  ref="menuWrapper" >
   	<ul>
   		<li class="menu-item" v-for="(item,index) in goods" :class="{current:currentIndex==index}" @click="selectMenu(index)">
   			<span class="text">
   			<span class="icon" :class="classMap[item.type]" v-show="item.type>0"></span>
   			{{item.name}}</span>
   		</li>
   	</ul>
   </div>
   <div class="foods-wrapper" ref="foodsWrapper">
   		<ul>
   			<li class="food-list food-list-hook" v-for="(item,index) in goods " >
   				<h1 class="title">{{item.name}}</h1>
   				<ul>
   					<li class="food-item" v-for="food in item.foods">
   						<div class="icon"><img :src="food.icon" width="57" height="57" alt=""></div>
   						<div class="content">
   							 <div class="name">{{food.name}}</div>
   							 <div class="desc">{{food.description}}</div>
   							 <div class="extra">

   								<span>月售{{food.sellCount}}份</span><span>好评率{{food.rating}}%</span>
   								
   							</div>
   							<div class="price">

   								<span class="newPrice">￥{{food.price}}</span><span class="oldPrice" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
   								<div class="cartcontrol-wrapper">
   									<cartcontrol :food='food' @ballsnum="getPos($event)"></cartcontrol>
   								</div>
   								
   							</div>
   						</div>
   						

   					</li>
   				</ul>
   			</li>
   		</ul>
   </div>
   <shopCart :delivery-price="seller.deliveryPrice" :min-price='seller.minPrice' :selectFoods="selectFoods" ref="shopCart"></shopCart>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import shopCart from '../shopcart/shopcart'
import cartcontrol from '../cartcontrol/cartcontrol'
export default {
components:{
	shopCart,
	cartcontrol,
},
  props:{
  	seller:{
  		type:Object
  	}
  },
  data(){
  	return{
  		goods:[],
  		heightList:[],
  		scrollY:0,
  		getPosition:[],

  	}
  },
  
  methods:{
  	getPos(value){
  		// 体验优化，异步执行下若动画
  		this.$nextTick(()=>{
  			this.$refs.shopCart.drop(value)
  		})
 	
  		
  	},
  	selectMenu(index){
  		let foodList=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
  		let el=foodList[index]
  		this.foodScroll.scrollToElement(el,300)
  	},
  	_foodScroll(){
  		this.menuScroll=new BScroll(this.$refs.menuWrapper,{
  			click:true
  		})
  		this.foodScroll=new BScroll(this.$refs.foodsWrapper,{
  		probeType:3,
  		click:true

  	})
  		this.foodScroll.on('scroll',(pos)=>{
  			this.scrollY=Math.abs(Math.round(pos.y))
  			// console.log(this.scrollY)	
  		})
  	},
  	_calculateHeight(){
  		let foodList=this.$refs.foodsWrapper.getElementsByClassName('food-list-hook')
  		let height=0
  		this.heightList.push(height)
  		for(let i=0;i<foodList.length;i++){
  			let item=foodList[i]
  			height+=item.clientHeight
  			this.heightList.push(height)
  		}
  	}

  },
  computed:{
  	currentIndex(){
  		for(let i=0;i<this.heightList.length;i++){
  			let height1=this.heightList[i]
  			let height2=this.heightList[i+1]
  			if(!height2 || (this.scrollY>=height1 && this.scrollY<height2)){
  				return i
  				
  			}
  		}
  		return 0
  	},
  // 	selectFoods(){
  // 		let foodsList=[]
  // 		this.goods.forEach((good)=>{
  // 			good.foods.forEach((food)=>{
  // 				if(food.count>0){
  // 					foodsList.push(food)
  // 				}
  // 			})
  // 		})
  // 		return foodsList
  // 	}
// selectFoods(){
// 	let foods=[]
// 	for(let i=0;i<this.goods.length;i++){
// 		this.goods[i].foods.forEach((food)=>{
// 			if(food.count>0){
// 				foods.push(food)
// 			}
// 		})
// 		return foods
// 	}
// }
selectFoods(){
	let foods=[]
	for(let i=0;i<this.goods.length;i++){
		for(let k=0;k<this.goods[i].foods.length;k++){
			if(this.goods[i].foods[k].count>0){
				foods.push(this.goods[i].foods[k])
			}
		}
	}
	return foods
}




  },
  created(){

  	this.classMap=['decrease','discount','guarantee','invoice','special']
  	
  	this.$http.get("https://wd7517823586mbplep.wilddogio.com/goods.json")
  	.then((response)=>{
  		if(response.status==200){
  			this.goods=response.body
  		  console.log(this.goods)
  		  console.log(this.goods[0].foods)
  		  this.$nextTick(()=>{
  	
  	this._foodScroll()
  	
  	this._calculateHeight()
  	})

  		}
  	})
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.goods{
		position: absolute;
		top:176px;
		bottom: 46px;
		width: 100%;
		display: flex;
		overflow: hidden;
		
	}
	/*侧边导航栏*/
	.goods .menu-wrapper{
		flex: 0 0 80px;
		width: 80px;
		background: #f3f5f7;
	}
	.goods .menu-wrapper .menu-item{
		padding: 0 12px;
		display: table;
		width: 56px;
		height: 54px;
		line-height: 14px;

	}
	.goods .menu-wrapper .menu-item.current{
		position: relative;
		top:-1px;
		z-index: 10;
		background: #fff;
		font-weight: 700;

	}
	.goods .menu-wrapper .menu-item.current .text{
		border:none;
	}
	.goods .menu-wrapper .menu-item .icon{
		vertical-align: top;
		display: inline-block;
		width: 16px;
		height: 16px;
	}
	.goods .menu-wrapper .menu-item .text{
		display: table-cell;
		vertical-align: middle;
		width: 56px;
		font-size: 12px;

		border-bottom: 1px solid rgba(7,17,27,0.1)
	}
	/*物品栏*/
	.goods .foods-wrapper{
		flex: 1;

	}
	.goods .foods-wrapper .food-list  .food-item{
		display: flex;
		margin:18px;
		padding-bottom: 18px;
		border-bottom: 1px solid rgba(7,17,27,0.1)

	}
	.goods .foods-wrapper .food-list  .food-item:last-child{
		margin-bottom: 0px;
	}
	
	.goods .foods-wrapper .food-list .title{
		padding-left: 14px;
		border-left: 2px solid #d9dde1;
		background: #f3f5f7;
		line-height: 26px;
		height: 26px;
		font-size: 12px;
		color: rgb(147,153,159)
	}
	.goods .foods-wrapper .food-list  .food-item .icon{
		flex: 0 0 57px;
		margin-right: 10px;
	}
	.goods .foods-wrapper .food-list  .food-item .content{
		flex: 1;
	}
	.goods .foods-wrapper .food-list  .food-item .content .name{
		margin: 2px 0px 8px 0px;
		height: 14px;
		line-height: 14px;
		color:rgb(7,17,27);
	}
	.goods .foods-wrapper .food-list  .food-item .content .desc,.goods .foods-wrapper .food-list  .food-item .content .extra{
		margin-bottom: 8px;
		font-size: 10px;
		line-height: 10px;
		color: rgb(147,157,159);

	}
		.goods .foods-wrapper .food-list  .food-item .content .desc{
			line-height: 12px;
		}
	.goods .foods-wrapper .food-list  .food-item .content .extra span{
		margin-right:12px;
	}
	.goods .foods-wrapper .food-list  .food-item .content .price{
		position: relative;
		margin-right: 8px;
		font-size: 14px;
		font-weight: bold;
		line-height: 24px;
	}
	 /*添加减少的控件(cartcontrol)*/
	.goods .foods-wrapper .food-list  .food-item .content .price .cartcontrol-wrapper{
		position: absolute;
		right: 0px;
		top:0px;
		font-size: 0px;

	}
	.goods .foods-wrapper .food-list  .food-item .content .price .newPrice{
		color: red;
	}
	.goods .foods-wrapper .food-list  .food-item .content .price .oldPrice{
		text-decoration: line-through; 
		color: rgb(147,153,159);
	}
	/*活动的图标*/
 .goods .menu-wrapper .menu-item .icon.decrease{
 	background-image: url('./decrease_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .goods .menu-wrapper .menu-item .icon.discount{
 	background-image: url('./discount_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .goods .menu-wrapper .menu-item .icon.guarantee{
 	background-image: url('./guarantee_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .goods .menu-wrapper .menu-item .icon.invoice{
 	background-image: url('./invoice_3@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	margin-right: 2px;
 	background-position: center;
 }
 .goods .menu-wrapper .menu-item .icon.special{
 	background-image: url('./special_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }


</style>



