<template>
  <div class="cartcontrol">
  	<transition name="move">
   	<div class="cart-decrease" v-show='food.count>0' @click="cartReduce()">
   		<i class="icon-remove_circle_outline"  ></i>
   	</div>
	</transition>
   	<transition name="fade">
   	<div class="cart-count" v-show='food.count>0'>{{food.count}}</div>
   </transition>
   	<div class="cart-add" @click="cartAdd()" ref="cartAdd">
   		<i class="icon-add_circle"></i>
   	</div>
   
   </div>
</template>

<script>
import Vue from 'vue'
export default {
  props:{
  	food:{
  		type:Object
  	}
  },
  data(){
    return {
        oTop:'',
        oLeft:''
    }
  },
  methods:{
  	cartAdd(){	
  		if(!this.food.count){
  			Vue.set(this.food,'count',1)
  		}else{
  		this.food.count++
  		}
      this.oTop=this.$refs.cartAdd.getBoundingClientRect().top
  		this.oLeft=this.$refs.cartAdd.getBoundingClientRect().left
      this.$emit("ballsnum",[this.oTop,this.oLeft])
  	},

  	cartReduce(){
  		this.food.count--
      this.$emit("hideMark")
  	}
  },
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.cartcontrol{
	}
	.cartcontrol .cart-decrease,.cartcontrol .cart-add {
		display: inline-block;
		padding: 6px;
		font-size: 24px;
		line-height: 24px;
		color: rgb(0,160,220)
	}
	.cartcontrol .cart-count{
		display: inline-block;
		width: 12px;
		vertical-align: top;
		padding-top: 6px;
		text-align: center;
		font-size: 10px;
		color: rgb(147,153,159);
		font-weight: 700;
		line-height: 24px;


	}
	/*动画*/
.move-enter-active, .move-leave-active{
	transform:  translateX(0px) rotate(0deg);
	transition:all 0.5s linear;
}
.move-enter,.move-leave-to{
	transform: translateX(24px) rotate(180deg);
	opacity: 0;
}	
.fade-enter-active, .fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
	

</style>



