<template>
  <div class="header">
   <div class="content-warpper">
   		<div class="avatar" >
   			<img width="64" height="64" :src="seller.avatar" alt="">
   		</div> 
   <div class="content">
   	<div class="title">
   		<span class="brand"></span>
   		<span class="name">{{seller.name}}</span>
   	</div>
   	<div class="description">
   		{{seller.description}}/{{seller.deliveryTime}}分钟送达
   	</div>
	<div class="supports" v-if="seller.supports">
		<span class="icon" :class="classMap[seller.supports[0].type]" ></span>
		<span class="text">{{seller.supports[0].description}}</span>
	</div>
   </div>
   <div class="supports-count" @click="showDetail()">
   		<span class="count" v-if="seller.supports">
   			{{seller.supports.length}}个
   		</span>
   		<i class="icon-keyboard_arrow_right"></i>
   </div>
   </div>
   <div class="bulletin-wrapper" @click="showDetail()">
   	  <span class="bulletin-title"></span><span class="bulletin-text">
   	  	{{seller.bulletin}}
   	  </span>	
   	  <i class="icon-keyboard_arrow_right"></i>
   </div>
   <div class="background">
   		<img :src="seller.avatar" alt="">
   </div>
   <transition name="fade">
   <div class="detail" v-show="detailShow">
   		<div class="detail-wrapper clearfix">
   			<div class="detail-main">
   				<h1 class="name">{{seller.name}}</h1>
   				<div class="star-wrapper">
   					<star :size='48' :score="seller.foodScore"></star>
   				</div>
   				<div class="title">
	   				<div class="line"></div>
	   				<div class="text">优惠信息</div>
	   				<div class="line"></div>
	   			</div>
	   			<ul class="supports" >
	   				<li class="supports-item" v-for='(item,index) in seller.supports'>
	   					<span class="icon" :class="classMap[item.type]"></span>
	   					<span class="text">{{item.description}}</span>
	   				</li>
	   			</ul>
	   			<div class="title">
	   				<div class="line"></div>
	   				<div class="text">商家公告</div>
	   				<div class="line"></div>
	   			</div>
	   			<div class="bulletin">
	   				<p class="content">
	   					{{seller.bulletin}}
	   				</p>
	   			</div>
   			</div>

   		</div>
   		<div class="detail-close" @click="hideDetail()">
   				<i class="icon-close"></i>
   		</div>
   </div>
   </transition>

  </div>
</template>

<script>
import star from '../start/start'
export default {
 props:{
 	seller:{
 		type:Object,
 	}
 },
 components:{
 	'star':star,
 },
 data(){
 	return{
 		detailShow:false
 	}
 },
methods:{
showDetail(){
	this.detailShow=true
},
hideDetail(){
	this.detailShow=false
}
},
mounted(){
// console.log(this.seller.supports.length)
},
created(){
	this.classMap=['decrease','discount','guarantee','invoice','special']
	console.log(this.classMap)
	console.log(this.seller.foodScore)
}

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header{
	position: relative;
		color:#fff;
		background-color: rgba(7,17,27,0.5);		
	}
.header .background img{
	position: absolute;
	top: 0px;
	left: 0px;
	z-index: -1;
	width: 100%;
	height: 100%;
	filter:blur(10px);
	overflow: hidden;
}
/*弹出框*/
 .header .detail{
 	position: fixed;
 	left: 0px;
 	top: 0px;
 	width: 100%;
 	height: 100%;
 	z-index: 100;
 	overflow: auto;
 	background-color: rgba(7,17,27,0.8);
 	backdrop-filter:blur(10px);
 	/*filter:blur(10px);*/
 }
 .clearfix{
 	display: inline-block;
 }

 .clearfix:after{
 	content: ".";
 	display: inline-block;
 	height: 0px;
 	clear: both;
 	visibility: hidden;
 }
 
 .header .detail .detail-wrapper{
 	min-height: 100%;
 	width: 100%;
 }
 .header .detail .detail-wrapper .detail-main{
 	margin-top: 64px;
 	padding-bottom: 64px;
 }
 .header .detail .detail-wrapper .detail-main .star-wrapper{
 	text-align: center;
 	margin-top: 16px;
 	padding: 2px 0px;
 }
 .header .detail .detail-wrapper .detail-main .name{
 	text-align: center;
 	font-weight: 700;
 	line-height: 16px;
 	height: 16px;
 }
 /*标题*/
 .header .detail .detail-wrapper .detail-main .title{
 	display: flex;
 	width: 80%;
 	margin:30px auto 24px auto;
 }
 .header .detail .detail-wrapper .detail-main .line{
 	flex: 1;
 	border-bottom: 1px solid rgba(255,255,255,0.2);
 	position: relative;
 	top: -5px;

 }
 .header .detail .detail-wrapper .detail-main .text{
 		vertical-align: top;
		text-align: center;
		padding: 0px 12px;
		line-height: 12px;
		font-size: 12px;
 }
.header .detail .detail-wrapper .detail-main .supports{
	width: 80%;
	margin:0 auto;
}
.header .detail .detail-wrapper .detail-main .supports .icon{
	display: inline-block;
	vertical-align: top;
	width: 16px;
	height:16px;
	background-size: 16px 16px;
	margin:2px 6px 12px 0px;
}
/*提醒框的公告*/
.header .detail .detail-wrapper .detail-main  .bulletin{
	width: 80%;
	margin:0px auto;
	
}
.header .detail .detail-wrapper .detail-main  .bulletin p{
	padding: 0px 12px;
	font-size: 12px;
	line-height: 24px;
	
}

 .header .detail .detail-close{
 	position: relative;

 	width: 32px;
 	height: 32px;
 	clear: both;
 	font-size: 32px;
 	margin: -64px auto 0 auto;
 }





.header .content-warpper{
	padding: 24px 12px 18px 24px;
	font-size: 0;
	position: relative;
	
}
.header .content-warpper .avatar{
	display: inline-block;
}	
.header .content-warpper .avatar img{
	border-radius: 2px;
} 
.header .content-warpper .content{
	display: inline-block;
	color:white;
	font-size: 14px;
}
.header .content-warpper .content{
	margin-left: 16px;
}
.header .content-warpper .content .title{
	
	margin-bottom: 8px;

}
.header .content-warpper .content .title .brand{
	display: inline-block;
	vertical-align: top;
	margin-right: 6px;
		height: 20px;
	width: 30px;
	background-image: url('./brand@2x.png');
	background-position: center;
	background-size: 30px 20px;
	background-repeat: no-repeat;
	

}
.header .content-warpper .content .title .name{
	
	margin-top: 6px;
	font-size: 16px;
	font-weight: bold;
	line-height: 18px;
	
}
.header .content-warpper .content .description{
	font-size: 12px;
	line-height: 12px;
	margin-bottom: 10px;
}
.header .content-warpper .content .supports .icon{
	display: inline-block;
	width: 12px;
	height: 10px;

}
 .header .content-warpper .content .supports{
 	font-size: 10px;
 	line-height: 12px;

 }
  .header .content-warpper .supports-count{
  	position: absolute;
  	right: 12px;
  	bottom: 18px;
  	padding: 0 8px;
  	height: 24px;
  	line-height: 24px;
  	border-radius: 14px;
  	background: rgba(0,0,0,0.2);
  }
  .header .content-warpper .supports-count .count{
    font-size: 10px;
  }
   .header .content-warpper .supports-count .icon-keyboard_arrow_right{
   	font-size: 10px;
   	height: 24px;
   	line-height: 24px;
   }
   /*公告*/
   .header .bulletin-wrapper{
   	position: relative;
   	height: 28px;
   	line-height: 28px;
   	text-overflow: ellipsis;
   	white-space: nowrap;
   	overflow: hidden;
    background-color: rgba(7,17,27,0.2);
    padding: 0 22px 0 12px;
   } 
 
   .header .bulletin-wrapper .bulletin-title{
   	display: inline-block;
   	margin-top:8px;
   	width: 22px;
   	height: 12px;
  	vertical-align: top;
  	margin-left: 12px;
  	font-size: 10px;
	background-image: url('./bulletin@3x.png');
	background-size: 22px 12px;
	background-repeat: no-repeat;
   }
    .header .bulletin-wrapper .bulletin-text{
    	vertical-align: top;
    	margin:0px 4px;
    	font-size: 10px;

    }
     .header .bulletin-wrapper .icon-keyboard_arrow_right{
		position: absolute;
		top: 9px;
		right: 12px;
		font-size: 10px;
     }
 /*活动的图标*/
 .header .content-warpper .content .supports .icon.decrease{
 	background-image: url('./decrease_1@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .content-warpper .content .supports .icon.discount{
 	background-image: url('./discount_1@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .content-warpper .content .supports .icon.guarantee{
 	background-image: url('./guarantee_1@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .content-warpper .content .supports .icon.invoice{
 	background-image: url('./invoice_1@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .content-warpper .content .supports .icon.special{
 	background-image: url('./special_1@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	background-position: center;
 }


/*活动的图标2*/
  .header .detail .detail-wrapper .detail-main .icon.decrease{
 	background-image: url('./decrease_2@3x.png');
 	
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .detail .detail-wrapper .detail-main .icon.discount{
 	background-image: url('./discount_2@3x.png');
 	
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .detail .detail-wrapper .detail-main .icon.guarantee{
 	background-image: url('./guarantee_2@3x.png');
 	
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .detail .detail-wrapper .detail-main .icon.invoice{
 	background-image: url('./invoice_2@3x.png');
 	
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .header .detail .detail-wrapper .detail-main .icon.special{
 	background-image: url('./special_2@3x.png');
 	
 	background-repeat: no-repeat;
 	background-position: center;
 }
 /*detail的动画*/
 .fade-enter-active, .fade-leave-active{
 	transition: all 1s;
 }
 .fade-enter, .fade-leave-to{
 	opacity: 0;
 }
</style>



